
export default function FileDown(){

  const fontZip="https://tl2gid.github.io/kakaofont.zip";

  const downFile=(url)=>{
    const fileName=url.split('/').pop();
    const aTag=document.createElement("a");
    aTag.href=url;
    aTag.setAttribute('download',fileName);
    document.body.appendChild(aTag);
    aTag.click();
    aTag.remove();
  }

  return (
    <div className="Login">
            <button class="subscr" type="button" onClick={()=>{downFile(fontZip)}}> 
                카카오기본서체 다운로드
                <i class="fa-solid fa-arrow-right"></i>             
            </button>
    </div>
  );
}