import {useState} from 'react';
import "../slick.css";
import "../slick-theme.css";
import Slider from "react-slick";

export default function ActualUse(){
    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
      };

    const [modal, setModal] = useState(false);
    const toggleModal = () => {
        setModal(!modal);
      };
    if(modal) {
      document.body.classList.add('active-modal')
    } else {
      document.body.classList.remove('active-modal')
    }
    
    return(
        <section id="actual-use" class="actual-use">
            <div class="a-wrap">
                <div className="a-box a-box1" onClick={toggleModal}></div>
                <div className="a-box a-box2" onClick={toggleModal}></div>
                <div className="a-box a-box3" onClick={toggleModal}></div>
                <div className="a-box a-box4" onClick={toggleModal}></div>
                <div className="a-box a-box5" onClick={toggleModal}></div>
                <div className="a-box a-box6" onClick={toggleModal}></div>
            </div>
            {modal &&(
                  <div class="modal overlay">
                        <button class="m-btn" onClick={toggleModal}><i class="fa-solid fa-xmark"></i></button>
                        <Slider {...settings}>
                            <div class="s-img1"></div>
                            <div class="s-img2"></div>
                            <div class="s-img3"></div>
                            <div class="s-img4"></div>
                            <div class="s-img5"></div>
                            <div class="s-img6"></div>
                        </Slider>
                    </div>
            )}
        </section>
    )
}



