import { useRef, useEffect} from "react";

import Header from "./components/Header";
import Main from "./components/Main";
import Info from "./components/Info";
import FontWeight from "./components/FontWeight";
import Emoticon from "./components/Emoticon";
import Example from "./components/Example"; 
import ActualUse from "./components/ActualUse";
import Download from "./components/Download";

export default function App() {  
    const targetRef = useRef(null);  
    const handleScroll = () => {
      
      const cont1=document.getElementById("cont1");
      const cont2=document.getElementById("cont2");
      const cont3=document.getElementById("cont3");
      const leftCont=document.getElementById("left-cont2");
      const rightCont=document.getElementById("right-cont2");
      var logo=document.getElementById("logo");

      function marginMinus(){
        leftCont.style.cssText="margin-left:-150px; transition:1s;";
        rightCont.style.cssText="margin-right:-150px; transition:1s;";
      }

      function iColor(){document.querySelector("i").style.color="#000";}
      

      if (window.scrollY>=300 && window.scrollY<=600) { 
        const animate1=[
          {opacity:0,transform:"translate3d(-10px, 50px ,0)"},
          {opacity:1,transform:"translate3d(0, 0 ,0)"}
        ]
        const animate2=[
          {opacity:0,transform:"translate3d(15px, 50px ,0)"},
          {opacity:1,transform:"translateZ(0)"}
        ]
        const dur = {duration: 1000,iterations: 1}
        const dur1={duration: 1000,delay:200}
        const dur2={duration: 1000,delay:300}

        cont1.animate(animate1,dur);
        cont2.animate(animate1,dur1);
        cont3.animate(animate2,dur2);

      }else if(window.scrollY > 700 && window.scrollY <= 1700){//info
        document.body.style.backgroundColor="#FFD36D";
        marginMinus();

      }else if(window.scrollY > 1700 && window.scrollY <= 2700){//fontWeight
        logo.style.fill="#000";
        iColor();
        leftCont.style.cssText="margin-left:150px; transition:1s;";
        rightCont.style.cssText="margin-right:150px; transition:1s;";
        document.body.style.backgroundColor="#FFD36D";

      }else if(window.scrollY > 2700 && window.scrollY <= 4300){//emoticon

        var svgColor1=document.getElementsByClassName("yellow");
        for( var i = 0; i < svgColor1.length; i++ ){
          var svg1 = svgColor1.item(i);
          svg1.style.cssText = "opacity:1;"; 
          const emoAni=[
            {transform:"scale(1) translateY(0)"},
            {transform:"scale(0.9) translateY(15px)"}
          ]
          const dur={duration:1000, fill:"forwards"}
          svg1.animate(emoAni,dur);
        }
        var svgColor2=document.getElementsByClassName("yellowCenter");
        for( var i = 0; i < svgColor2.length; i++ ){
          var svg1 = svgColor2.item(i);
          svg1.style.cssText = "opacity:1;"; 
          const emoAni=[
            {transform:"scale(1)"},
            {transform:"scale(0.8)"}
          ]
          const dur={duration:1000, fill:"forwards"}
          svg1.animate(emoAni,dur);
        }

        
        logo.style.fill="#FFCD00";
        document.querySelector("i").style.color="#FFCD00";
        document.querySelector(".menu").style.color="#FFCD00";
        document.body.style.cssText="background:#333; transition:0.6s;";
        marginMinus();

      }else if(window.scrollY > 4300 && window.scrollY <= 4500){
        document.body.style.backgroundColor="#FFD36D"; 
      }else{
        iColor();
        logo.style.fill="#1E1E1E";
        document.querySelector(".menu").style.color="#000";
        leftCont.style.cssText=marginMinus;
        document.body.style.backgroundColor="#FFD36D"; 
        marginMinus();
      }
    };
  
    useEffect(() => {    
      const timer = setInterval(() => {
        window.addEventListener("scroll", handleScroll);
      }, 100);
      return () => {
        clearInterval(timer);
        window.removeEventListener("scroll", handleScroll);
      };
    }, []);

    return (
      <div className="App" ref={targetRef}>
        <div className="wrap">
            <Header/>
            <Main/>
            <Info />
            <FontWeight />
            <Emoticon />
            <Example />
            <ActualUse />
            <Download />
        </div>       
    </div>
     );
  }
